.movie_detail_details {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 60px;
  border: 2px solid #c8c8c8;
  padding: 20px;
  border-radius: 10px;
}

.movie_detail_image {
  flex: 1 0 20px;
  margin-right: 20px;
}

.movie_detail_image img {
  width: 100%;
  height: auto;
}

.movie_detail_info {
  flex: 2 1 500px;
  margin-left: 20px;
}

.movie_detail_info h1 {
  margin-top: 0;
}

.movie_detail_info p {
  margin-bottom: 10px;
}

.goBack {
  position: absolute;
  top: 70px;
  left: 20px; 
}

.CastReviews {
  display: flex;
  flex-direction: column;
  margin-top: 5px;
  border-top: 2px solid #c8c8c8;
  padding-top: 0;
}

.margin {
  margin-right: 20px;
}

.inform {
  display: flex;
  align-items: center;
}

.titel_info {
  margin-left: 20px;
}